import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import BackgroundDiv from "./BackgroundDiv";
import colors from "../../constants/colors";

const styles = {
  contentDiv: {
    padding: "125px 102px",
    width: "90%",
    color: colors.black,
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: "sans-serif"
  },
  textDiv: {
    paddingLeft: "70px",
    paddingRight: "40px",
    paddingTop: "150px",

    width: "25vw",
    minWidth: "350px",
    minHeight: "350px",
    overflow: "scroll",

    position: "absolute",
    color: colors.black,
    fontSize: "16px",
    lineHeight: "2.0em",
    fontFamily: "sans-serif",
    zIndex: "100",
    transition: "opacity 2s",
    opacity: "1"
  },
  textDivHidden: {
    padding: "30px",
    width: "28%",
    position: "absolute",
    color: colors.black,
    opacity: "0",
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: " sans-serif",
    zIndex: "100",

  }
};

const ContentItem = ({ classes, text, state }) => (
  <div className={classes.contentDiv}>
    <div className={state === 1 ? classes.textDiv : classes.textDivHidden}>
      {text}
    </div>
    <div className="trails-clip">
      <BackgroundDiv state={state} />
    </div>
  </div>
);

ContentItem.propTypes = {
  classes: PropTypes.object,
  text: PropTypes.string,
  state: PropTypes.number
};

export default withStyles(styles)(ContentItem);
