/* eslint-disable */
import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Parallax } from "react-spring/renderprops-addons";
import "intersection-observer";
import VideoSection from "./components/VideoSection";
import WeBelieveSection from "./components/WeBelieveSection";
import AnimationSection from "./components/AnimationSection";
import WhatIsSection from "./components/WhatIsSection";
import OptionsSection from "./components/OptionsSection";
import VideoGallerySection from "./components/VideoGallerySection";
import MobgennersSection from "./components/MobgennersSection";
import ContactSection from "./components/ContactSection";
import ByTheWaySection from "./components/ByTheWaySection";
import RightNavigation from "./components/RightNavigation";
import Utils from "./utils/utils";
import colors from "./constants/colors";
import ribbons from "./constants/ribbons";
import mobgenners from "./constants/mobgenners";
import CookieConsent from "react-cookie-consent";

import "./App.css";
import './assets/css/fonts.css'

const styles = {
  parallax: {
    margin: "0"
  }
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.iniAnimation = 28;
    this.endAnimation = 378;
    this.iniMotion = 0;
    this.endMotion = 239;
    this.limitChangeBGColor = 200;
    this.handleScroll = value => {
      this.parallax && this.parallax.scrollTo(value);
    };
    this.state = {
      scrolling: true,
      currentPage: 1,
      yPosition: 0,
      startAnimation: 0,
      indexAnimation: this.iniAnimation,
      indexMotion: this.iniMotion,
      onClickMenu: false,
      scrollDown: true
    };
    this.handleOnScroll = this.handleOnScroll.bind(this);
  }

  handleGoToPage = offset => {
    if (offset !== 2) {
      this.setState({
        onClickMenu: true
      });
    }

    this.handleScroll(offset);
    this.pageOnChange(offset);
  };

  pageOnChange = number => {
    this.setState({
      currentPage: number
    });
  };

  getNextIndex(idx) {
    if (idx > this.endAnimation) {
      return this.iniAnimation;
    }
    return idx + 1;
  }

  setIndexes(idx) {
    this.setState({
      indexAnimation: idx,
      nextAnimation: this.getNextIndex(idx)
    });
  }

  getNextMGIndex(idx) {
    if (idx > this.endMotion) {
      return this.iniMotion;
    }
    return idx + 1;
  }

  setMGIndexes(idx) {
    this.setState({
      indexMotion: idx,
      nextAnimation: this.getNextMGIndex(idx)
    });
  }


  handleOnScroll() {
    const page = parseInt(this.parallax.current / this.parallax.space, 0);

    if (page !== this.parallax.offset) {
      this.parallax.offset = page;
      this.pageOnChange(page + 1);
    }

    if (this.parallax.current > this.state.yPosition && !this.state.scrollDown) {
      this.setState({
        scrollDown: true
      });
    }
    if (this.parallax.current < this.state.yPosition && this.state.scrollDown) {
      this.setState({
        scrollDown: false
      });
    }
    /* Start Ribbons animation */
    if (this.parallax.offset === 2 ) {
      this.setState({
        isStopped: false
      });
      var idInterval = setInterval(() => {
        if (!this.state.isStopped) {
          setTimeout(() => {
            this.setIndexes(this.getNextIndex(this.state.indexAnimation));
          }, 0); // same delay as in the css transition here
        }
        else {
          clearInterval(idInterval);
        }
      }, 1000/24);
    }

    if (this.parallax.offset !== 2) {
      this.setState({
        isStopped: true
      });
      clearInterval(idInterval);
    }
    /* End Ribbons animation */

    /* Start Motion animation */
    if (this.parallax.offset === 4 ) {
      this.setState({
        isStoppedMotion: false
      });
      var idIntervalMotion = setInterval(() => {
        if (!this.state.isStoppedMotion) {
          setTimeout(() => {
            this.setMGIndexes(this.getNextMGIndex(this.state.indexMotion));
          }, 0); // same delay as in the css transition here
        }
        else {
          clearInterval(idIntervalMotion);
        }
      }, 1000/24);
    }

    if (this.parallax.offset !== 4) {
      this.setState({
        isStoppedMotion: true
      });
      clearInterval(idIntervalMotion);
    }
    /* End Ribbons animation */
    this.setState({
      yPosition: this.parallax.current
    });

    const position = ((this.parallax.offset+1) * this.parallax.space);
    const diff = (position - this.parallax.current);
    if (diff < this.limitChangeBGColor) {
      let color = colors.transparent;
      if (this.state.scrollDown) {
        if (this.parallax.offset === 0) color = colors.section2;
        if (this.parallax.offset === 1) color = colors.section3;
        if (this.parallax.offset === 2) color = colors.section4;
        if (this.parallax.offset === 3) color = colors.section4;
        if (this.parallax.offset === 4) color = colors.section4;
        if (this.parallax.offset === 5) color = colors.section5;
        if (this.parallax.offset === 6) color = colors.section6;
        if (this.parallax.offset === 7) color = colors.section7;
        if (this.parallax.offset === 8) color = colors.section7;
        if (this.parallax.offset === 9) color = colors.section8;
        if (this.parallax.offset === 10) color = colors.section8b;
        if (this.parallax.offset === 11) color = colors.section9;
        if (this.parallax.offset === 12) color = colors.section9;
        if (this.parallax.offset === 13) color = colors.section9;

        document.getElementById("root").style.background = color;
      }
      else {
        if (this.parallax.offset === 0) color = colors.section1;
        if (this.parallax.offset === 1) color = colors.section2;
        if (this.parallax.offset === 2) color = colors.section3;
        if (this.parallax.offset === 3) color = colors.section4;
        if (this.parallax.offset === 4) color = colors.section4;
        if (this.parallax.offset === 5) color = colors.section4;
        if (this.parallax.offset === 6) color = colors.section5;
        if (this.parallax.offset === 7) color = colors.section6;
        if (this.parallax.offset === 8) color = colors.section7;
        if (this.parallax.offset === 9) color = colors.section7;
        if (this.parallax.offset === 10) color = colors.section8;
        if (this.parallax.offset === 11) color = colors.section8b;
        if (this.parallax.offset === 12) color = colors.section9;
        if (this.parallax.offset === 13) color = colors.section9;
        document.getElementById("root").style.background = color;
      }
    }
  }

  render() {
    const { currentPage, indexAnimation, indexMotion, scrolling } = this.state;
    const { classes } = this.props;
    const config = {tension: 120, friction: 20};
    return (
      <React.Fragment>
        <div onScroll={this.handleOnScroll}>
          <Parallax
            className={classes.parallax}
            ref={ref => (this.parallax = ref)}
            pages={14}
            scrolling={scrolling}
            config = {config}
          >
            <VideoSection factor={1} offset={0} color={colors.transparent} onGoToPage={this.handleGoToPage} />
            <WeBelieveSection factor={1} offset={1} page={currentPage} color={colors.transparent} />
            <AnimationSection indexAnimation={indexAnimation} factor={1} offset={2} color={colors.transparent} />
            <WhatIsSection indexMotion={indexMotion} factor={2} offset={3} page={this.state.currentPage} color={colors.transparent} />
            <OptionsSection factor={1} offset={6} color={colors.transparent} />
            <VideoGallerySection factor={1.5} page={currentPage} offset={7} color={colors.transparent} />
            <MobgennersSection factor={2.5} page={currentPage} offset={8.5} color={colors.transparent} />
            <ContactSection factor={2} offset={10.5} page={currentPage} color={colors.transparent} />
            <ByTheWaySection factor={1} offset={12.5} color={colors.transparent} />
          </Parallax>
          <RightNavigation
            currentPage={currentPage}
            onGoToPage={this.handleGoToPage}
          />
        </div>

        <CookieConsent
            location="bottom"
            buttonText="I understand"
            cookieName="Google Analytics"
            style={{ background: colors.black }}
            buttonStyle={{ background: colors.section5, fontSize: "13px" }}
            expires={150}
        >
            This website uses cookies to enhance the user experience.{" "}
            <span style={{ fontSize: "10px" }}>
            </span>
        </CookieConsent>
      </React.Fragment>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object
};

export default withStyles(styles)(App);
