import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { Responsive } from "semantic-ui-react";

import Gallery from "./Gallery";
import texts from "../../constants/texts";
import colors from "../../constants/colors";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    height: "150%",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "flex-start",
    flexDirection: "column",
    paddingTop: "20%",
    color: colors.white
  }),
  title: {
    display: "block",
    fontSize: "72px",
    marginBottom: "450px",
    marginLeft: "80%",
    textAlign: "right",
    transition: "margin-bottom 1s",
    width: "400px"

  },

  titleMobile: {
    display: "block",
    width: "100%",
    fontSize: "64px",
    marginLeft: "10vw",
    textAlign: "left",
    marginBottom: "120px",

    transition: "margin-bottom 1s",
  },

  titleAfter: {
    display: "block",
    fontSize: "72px",
    marginBottom: "120px",
    marginLeft: "80%",
    textAlign: "right",
    width: "400px",
    transition: "margin-bottom 1s",

  },

  textDiv: {
    width: "40%",
    margin: "50px 0px",
    textAlign: "left",
    fontFamily: "sans-serif",
    fontSize: "15px",
    lineHeight: "25px",
    position: "absolute",
    left: "20vw",

    color: colors.white
  },

  textDivMobile: {
    width: "80vw",
    marginLeft: "10vw",
    marginTop: "50px",
    textAlign: "left",
    fontSize: "90%",
    lineHeight: "25px",
    marginBottom: "10px",
    color: colors.white
  }
};

const VideoGallerySection = ({ classes, factor, offset, page }) => (
  <ParallaxLayer factor={factor} offset={offset} speed={0.1}>
    <div className={classes.componentDiv}>
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <h1 className={page===7 ? classes.title : classes.titleAfter}>{texts.S5_TITLE}</h1>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <h1 className={classes.titleMobile}>{texts.S5_TITLE}</h1>
      </Responsive>

      <Gallery />
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <div className={classes.textDiv} id="text-blurb">
         {texts.S5_TEXTS[0]}
        </div>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <div className={classes.textDivMobile} id="text-blurb">
        {texts.S5_TEXTS[0]}
       </div>
      </Responsive>

    </div>
  </ParallaxLayer>
);

VideoGallerySection.propTypes = {
  classes: PropTypes.object,
  factor: PropTypes.number,
  offset: PropTypes.number
};

export default withStyles(styles)(VideoGallerySection);
