import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { Responsive } from "semantic-ui-react";
import ReactRevealText from "react-reveal-text";


import TextLoop from "react-text-loop";
import texts from "../../constants/texts";
import colors from "../../constants/colors";
import backgroundImage from "../../assets/img/ContactBackground.png";

const styles = {
  sectionDiv: props => ({
    backgroundColor: props.color,
    width: "100%"
  }),
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    height: "200vh",
    width: "100%",
    margin: "0",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  }),
  responsiveDiv: {
    width: "100%"
  },
  mainText: {
    textAlign: "center",
    width: "100%",
    height: "100vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  },
  addressTextDiv: {
    background: `linear-gradient(rgba(173, 232, 248, 0.5),rgba(173, 232, 248, 0.5)), url(${backgroundImage})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center",
    textAlign: "left",
    width: "100%",
    height: "100vh",
    display: "flex",
    alignItems: "center"
  },
  addressText: {
    paddingLeft: "10%"
  },
  normalText: {
    fontWeight: "bold",
    fontSize: "64px",
    width: "fit-content",
    margin: "0px"
  },
  normalTextMobile: {
    fontWeight: "bold",
    fontSize: "calc(20px + 4vw)",
    width: "fit-content",
    margin: "10px 30px"
  },

  selected: {
    color: colors.white,
    backgroundColor: colors.black,
    padding: "2px 10px"
  },
  smallText: {
    fontWeight: "bold",
    marginTop: "40px"
  }
};

const ContactSection = ({ classes, offset, page }) => (
  <ParallaxLayer offset={offset} speed={0} className={classes.sectionDiv}>
    <div className={classes.componentDiv}>
      <Responsive minWidth={Responsive.onlyTablet.minWidth} className={classes.responsiveDiv}>
        <div className={classes.mainText}>
          <div className={classes.normalText}>{texts.S7_LINE1_TXT1}</div>
          <div className={classes.normalText}>
          <ReactRevealText transitionTime={100} show={page === 11}>
              {texts.S7_LINE2_TXT1} 
          </ReactRevealText>
          </div>
          <div className={classes.smallText}>{texts.S7_LINE3_TXT1}</div>
        </div>
        <div className={classes.addressTextDiv}>
          <div className={classes.addressText}>
            <div className={`${classes.normalText} ${classes.overlay}`}>
              {texts.S7_LINE4_TXT1}
            </div>
            <div className={`${classes.normalText} ${classes.selected}`}>
              <TextLoop mask>
                <span className={classes.effectText}>{texts.S7_LINE5_TXT1A}</span>
                <span className={classes.effectText}>{texts.S7_LINE5_TXT1B}</span>
                <span className={classes.effectText}>{texts.S7_LINE5_TXT1C}</span>
              </TextLoop>
            </div>
            <div className={classes.normalText}>{texts.S7_LINE6_TXT1}</div>
            <div className={classes.normalText}>{texts.S7_LINE7_TXT1}</div>
          </div>
        </div>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <div className={classes.mainText}>
          <div className={classes.normalTextMobile}>{texts.S7_LINE1_TXT1}</div>
          <div className={classes.normalTextMobile}>
          <ReactRevealText transitionTime={100} show={page === 11}>
              {texts.S7_LINE2_TXT1} 
          </ReactRevealText>
          </div>
          <div className={classes.smallText}>{texts.S7_LINE3_TXT1}</div>
        </div>
        <div className={classes.addressText}>
          <div>
            <div className={`${classes.normalTextMobile} ${classes.overlay}`}>
              {texts.S7_LINE4_TXT1}
            </div>
            <div className={`${classes.normalTextMobile} ${classes.selected}`}>
              <TextLoop mask>
                <span className={classes.effectText}>{texts.S7_LINE5_TXT1A}</span>
              </TextLoop>
            </div>
            <div className={classes.normalTextMobile}>{texts.S7_LINE6_TXT1}</div>
            <div className={classes.normalTextMobile}>{texts.S7_LINE7_TXT1}</div>
          </div>
        </div>
      </Responsive>
    </div>
  </ParallaxLayer>
);

ContactSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(ContactSection);
