import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Responsive } from "semantic-ui-react";
import { InView } from "react-intersection-observer";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import ListItem from "./ListItem";
import ListItemMobile from "./ListItemMobile";
import MobileModal from "./MobileModal";

import ContentItem from "./ContentItem";
import colors from "../../constants/colors";
import content from "../../constants/contentOptionsSection";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    height: "100vh",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center"
  }),
  leftDiv: {
    width: "45%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  },
  leftDivMobile: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  },
  rightDiv: {
    width: "598px",
    marginLeft: "0%",
    height: "598px",
    display: "flex"
  },
  listDiv: {
    width: "100%",
    height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    overflow: "hidden"
  },
  ul: {
    width: "100%",
    margin: "0",
    listStyleType: "none",
    paddingLeft: "0px"
  },
  contentItemDiv: {
    backgroundColor: colors.transparent,
    display: "flex",
    marginLeft: "0%",
    alignItems: "center",
    justifyContent: "center",
    width: "598px",
    height: "598px"
  }
};

const OptionsSection = ({ classes, offset }) => {
  const [isVisible, setIsVisible] = useState(0);
  const [animState, setAnimState] = useState(1);
  const [modalState, setModalState] = useState(false);
  const [inViewValue, setInView] = useState(false);
  const [isFirstView, setIsFirstView] = useState(true);
  const [isMobile, setIsMobile] = useState(false);
  const [element, setElement] = useState(" ");

  const handleShowContent = e => {
    const obj = content.find(object => object.i === e);
    setElement(obj);
    setIsVisible(obj.i);

    if (obj.i !== isVisible) {
      setAnimState(0);
      setTimeout(() => {
        setAnimState(1);
      }, 500);
    }
  };

  const handleShowContentMobile = e => {
    const obj = content.find(object => object.i === e);
    setElement(obj);
    setModalState(true);
  };

  const handleHideContentMobile = e => {
    setModalState(false);
  };

  const handleInView = (inView, mobile) => {
    setInView(inView);
    setIsMobile(mobile);
  };

  useEffect(() => {
    if (inViewValue && isFirstView) {
      setIsFirstView(false);
      if (!isMobile) handleShowContent(1);
    }
  });

  return (
    <ParallaxLayer offset={offset} speed={0.1}>
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <div className={classes.componentDiv}>
          <div className={classes.leftDiv}>
            <div className={classes.listDiv}>
              <InView
                as="div"
                onChange={inView => handleInView(inView, false)}
              />
              <ul className={classes.ul}>
                {content.map(e => {
                  return (
                    <ListItem
                      key={e.i}
                      index={e.i}
                      title={e.title}
                      isVisible={isVisible}
                      onShowContent={handleShowContent}
                      className={classes.red}
                    />
                  );
                })}
              </ul>
            </div>
          </div>
          <div className={classes.rightDiv}>
            {isVisible !== 0 && (
              <div className={classes.contentItemDiv}>
                <ContentItem text={element.text} state={animState} />
              </div>
            )}
          </div>
        </div>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <div className={classes.componentDiv}>
          <div className={classes.leftDivMobile}>
            <div className={classes.listDiv}>
              <InView
                as="div"
                onChange={inView => handleInView(inView, true)}
              />
              <ul className={classes.ul}>
                {content.map(e => {
                  return (
                    <ListItemMobile
                      key={e.i}
                      index={e.i}
                      title={e.title}
                      isVisible={isVisible}
                      onShowContent={handleShowContentMobile}
                      className={classes.red}
                    />
                  );
                })}
              </ul>
            </div>
          </div>
          <MobileModal
            show={modalState}
            text={element.text}
            handleClose={handleHideContentMobile}
          />
        </div>
      </Responsive>
    </ParallaxLayer>
  );
};

OptionsSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(OptionsSection);
