import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import ReactHtmlParser from "react-html-parser";

import colors from "../../constants/colors";


const styles = {
  liClass: {
    listStyle: "none",
    marginLeft: "10px",
    "&:last-child": {
      marginTop: "60px"
    },
  },
  button: {
    textAlign: "left",

    border: "0px",
    backgroundColor: colors.transparent,
    color: colors.black,
    fontSize: "32px",
    lineHeight: "44px",
    letterSpacing: "-0.58px",
    fontFamily: "Graphik-Bold, sans-serif",
    fontWeight: "bold",
    "&:hover": {
      color: colors.white,
      cursor: "pointer !important"
    },
    "&:focus": {
      outline: "none"
    }
  },
  active: {
    color: `${colors.white} !important`,
    "&:focus": {
      outline: "none"
    }
  }
};

const ListItem = ({ classes, index, isVisible, title, onShowContent }) => (
  <li className={classes.liClass}>
    <button
      type="button"
      className={`${classes.button} ${
        isVisible === index ? classes.active : ""
      }`}
      onClick={() => onShowContent(index)}
    >
      {ReactHtmlParser(title)}
    </button>
  </li>
);

ListItem.propTypes = {
  classes: PropTypes.object,
  index: PropTypes.number,
  isVisible: PropTypes.number,
  title: PropTypes.string,
  onShowContent: PropTypes.func
};

export default withStyles(styles)(ListItem);
