/* eslint-disable react/no-array-index-key */
import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import ImageComponent from "./ImageComponent";
import ribbons from "../../constants/ribbons";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    height: "100vh",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center"
  }),
  imageDiv: {
    width: "100%",
    display: "block"
  },
  logo: {
    position: "relative",
    width: "100%"
  }
};

const AnimationSection = ({ classes, factor, offset, indexAnimation }) => (
  <ParallaxLayer factor={factor} offset={offset} speed={0}>
    <div className={classes.componentDiv}>
      <div className={classes.ImageDiv}>
        {ribbons.map((e, i) => {
          return (
            <ImageComponent
              key={`t${i}`}
              index={i}
              isVisible={i === indexAnimation}
            />
          );
        })}
      </div>
    </div>
  </ParallaxLayer>
);

AnimationSection.propTypes = {
  classes: PropTypes.object,
  factor: PropTypes.number,
  offset: PropTypes.number,
  indexAnimation: PropTypes.number
};

export default withStyles(styles)(AnimationSection);
