/* eslint-disable */
import React, { useState } from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Player, ControlBar, BigPlayButton } from "video-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import colors from "../../constants/colors";
import IntroVideo from "../../assets/videos/One.mp4";
import FullVideo from "../../assets/videos/One.mp4";
import "../../assets/css/video-react.css";

const styles = {
	componentDiv: {
		backgroundColor: colors.section2,
		overflow: "hidden",
		height: "100%",
		width: "100%",
		display: "flex",
		position: "relative",
		alignItems: "center",
		justifyContent: "center",
		flexDirection: "column"
  },
  videoDiv: {
    height: "100% !important",
    minHeight: "100% !important",
    display: "block",
    backgroundColor: colors.orange,
    "& .video-react .video-react-video": {
      width: "auto !important",
      height: "100% !important",
      position: "absolute",
      top: "50%",
      left: "50%",
      width: "auto",
      height: "auto",
      minWidth: "100%",
      minHeight: "100%",
      display: "inline-block",
      transform: "translateX(-50%) translateY(-50%)",
    },
  },
	playDiv: {
		marginTop: "0%",
    zIndex: "999",
    padding: "0",
    cursor: "pointer",
    opacity: "1",
    border: "0.06666em solid #fff",
    backgroundColor: "#2B333F",
    backgroundColor: "rgba(43, 51, 63, 0.7)",
    borderRadius: "0.4em",
    transition: "all 0.4s",
    width: "8em",
    height: "4em",
    position: "absolute",
    //marginLeft: "5 0vh",
    //marginTop: "50vh"
	},
	playIcon: {
		color: colors.white,
		width: "100% !important",
    height: "22px !important",
    marginTop: "7px",
    display: "flex",

	}
};

class VideoComponent extends React.Component {
	constructor(props, context) {
		super(props, context);

		this.state = {
			player: null,
			source: IntroVideo,
			isPlaying: false,
			isFullscreen: false
		};

		this.play = this.play.bind(this);
		this.pause = this.pause.bind(this);
		this.load = this.load.bind(this);
		this.handleFullScreen = this.handleFullScreen.bind(this);
	}

	componentDidMount() {
		document.addEventListener("keydown", this.handleFullScreen);
		document.addEventListener("fullscreenchange", this.handleFullScreen);
		document.addEventListener("mozfullscreenchange", this.handleFullScreen);
		document.addEventListener("webkitfullscreenchange", this.handleFullScreen);
		document.addEventListener("msfullscreenchange", this.handleFullScreen);
		this.refs.player.subscribeToStateChange(this.handleStateChange.bind(this));
	}

	componentWillUnmount() {
		document.removeEventListener("fullscreenchange", this.handleFullScreen);
		document.removeEventListener("mozfullscreenchange", this.handleFullScreen);
		document.removeEventListener("webkitfullscreenchange", this.handleFullScreen);
		document.removeEventListener("msfullscreenchange", this.handleFullScreen);
		this.refs.player.pause();
	}

	componentDidUpdate(prevProps, prevState) {
		if (this.state.source != prevState.source) {
			this.refs.player.load();
		}
	}

	handleStateChange(state, prevState) {
		this.setState({
			player: state
		});
	}

	play() {
		this.refs.player.play();
	}

	pause() {
		this.refs.player.pause();
	}

	load() {
		this.refs.player.load();
	}

	toggleFullscreen() {
		this.refs.player.toggleFullscreen();
	}

	changeSource() {
		return () => {
			this.setState({
				source: FullVideo,
				isPlaying: true
			});
			this.refs.player.controls = true;
			this.refs.player.muted = false;
			this.toggleFullscreen();
			this.load();
		};
	}

	setIntroVideo() {
		this.refs.player.muted = true;
		this.refs.player.controls = false;
		this.setState({
			source: IntroVideo,
			isPlaying: false
		});
	}

	handleFullScreen(e) {
		this.setState({ isFullscreen: !this.state.isFullscreen });
		if (!this.state.isFullscreen) {
			this.setIntroVideo();
		}
	}

	render() {
		const { source, isPlaying } = this.state;
		const { classes } = this.props;
		return (
			<div className={classes.componentDiv}>
        <div className={classes.videoDiv}>
          <Player ref="player" videoId="video1" autoPlay loop muted
            fluid={false}
            width="auto"
            height="100%"
          >
            <source src={source} type="video/mp4" />
            <BigPlayButton position="center" />
            <ControlBar disableDefaultControls />
          </Player>
        </div>
				{!isPlaying && (
					<div className={`${classes.playDiv} video-react video-react-big-play-button`}>
						<FontAwesomeIcon
							icon={faPlay}
							onClick={this.changeSource()}
							className={classes.playIcon}
						/>
					</div>
				)}
			</div>
		);
	}
}

VideoComponent.propTypes = {
	classes: PropTypes.object
};

export default withStyles(styles)(VideoComponent);
