import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { InView } from "react-intersection-observer";

import CountUp from "react-countup";

const styles = {
  lineText: {
    margin: 0
  }
};

const CountUpComponent = ({ classes, start, end, text }) => (
  <div className={classes.lineText}>
    <CountUp start={start} end={end}>
      {({ countUpRef, start }) => (
        <div>
          <InView as="div" threshold={0.3} onChange={start} />
          <span ref={countUpRef} />
          {text}
        </div>
      )}
    </CountUp>
  </div>
);

CountUpComponent.propTypes = {
  classes: PropTypes.object,
  start: PropTypes.number,
  end: PropTypes.number,
  text: PropTypes.string
};

export default withStyles(styles)(CountUpComponent);
