import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Responsive } from "semantic-ui-react";

import { ParallaxLayer } from "react-spring/renderprops-addons";
import CountUpComponent from "./CountUpComponent";
import AnimatedGallery from "./AnimatedGallery";
import texts from "../../constants/texts";

const styles = {
  sectionDiv: props => ({
    backgroundColor: props.color,
    width: "100%",
    marginTop: "100px"
  }),
  componentDiv: props => ({
    backgroundColor: props.color,
    width: "80%",
    margin: "12%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  }),
  textDiv: {
    width: "90%",
    fontWeight: "bolder",
    fontSize: "300%",
    position: "absolute",
    left: "12%",
    top: "0px"
  },

  textDivMobile: {
    width: "90%",
    fontWeight: "bolder",
    fontSize: "calc(20px + 4vw)",
    marginTop: "-120px",
    position: "absolute",
    left: "12%",
    top: "0px"
  },
  galleryDiv: {
    width: "100%"
  }
};

// Equal spacing -square images.

const MobgennersSection = ({ classes, offset, page }) => (
  <ParallaxLayer
    offset={offset}
    factor={2.5}
    speed={0}
    className={classes.sectionDiv}
  >
    <div className={classes.componentDiv}>
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <div className={classes.textDiv}>
          <CountUpComponent
            start={0}
            end={texts.S6_LINE1_NUM}
            text={texts.S6_LINE1_TXT1}
          />
          <CountUpComponent
            start={0}
            end={texts.S6_LINE2_NUM}
            text={texts.S6_LINE2_TXT1}
          />
          <CountUpComponent
            start={0}
            end={texts.S6_LINE3_NUM}
            text={texts.S6_LINE3_TXT1}
          />
        </div>
      </Responsive>

      <Responsive {...Responsive.onlyMobile}>
        <div className={classes.textDivMobile}>
          <CountUpComponent
            start={0}
            end={texts.S6_LINE1_NUM}
            text={texts.S6_LINE1_TXT1}
            page={page}
          />
          <CountUpComponent
            start={0}
            end={texts.S6_LINE2_NUM}
            text={texts.S6_LINE2_TXT1}
            page={page}
          />
          <CountUpComponent
            start={0}
            end={texts.S6_LINE3_NUM}
            text={texts.S6_LINE3_TXT1}
            page={page}
          />
        </div>
      </Responsive>

      <div className={classes.galleryDiv}>
        <AnimatedGallery minwidth={Responsive.onlyTablet.minWidth} />
      </div>
    </div>
  </ParallaxLayer>
);

MobgennersSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(MobgennersSection);
