import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import VideoComponent from "./VideoComponent";
import IntroVideo from "../../assets/videos/Lab.mp4";

import Header from "../Header";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    width: "100%",
    height: "100%",
    display: "block",
    position: "relative"
  }),
  contentDiv: {
    width: "100%",
    height: "100vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  }
};

const VideoSection = ({ classes, factor, offset, onGoToPage }) => (
  <ParallaxLayer factor={factor} offset={offset} speed={0.1}>
    <Header onGoToPage={onGoToPage} />
    <div className={classes.componentDiv}>
      <div className={classes.contentDiv}>
        <VideoComponent video={IntroVideo} />
      </div>
    </div>
  </ParallaxLayer>
);

VideoSection.propTypes = {
  classes: PropTypes.object,
  factor: PropTypes.number,
  offset: PropTypes.number,
  onGoToPage: PropTypes.func
};

export default withStyles(styles)(VideoSection);
