import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { Responsive } from "semantic-ui-react";
import MotionComponent from "./MotionComponent";
import motions from "../../constants/motion";
import colors from "../../constants/colors";
import texts from "../../constants/texts";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    height: "190%",
    width: "90%",
    display: "flex",
    position: "relative",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    paddingLeft: "10%",
    paddingTop: "10%",
    color: colors.black
  }),
  elementDiv: {
    width: "90%",
    display: "flex",
    position: "relative",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    paddingLeft: "10%",
    paddingTop: "0%",
    opacity: "1",
    transition: "opacity 1s"
  },
  noDiv: {
    width: "90%",
    display: "flex",
    position: "relative",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    paddingLeft: "10%",
    paddingTop: "0%",
    opacity: "0",
    transition: "opacity 1s"
  },
  cardDiv: {
    backgroundColor: colors.grey,
    height: "80%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  blockBackground: {
    width: "80%",
    height: "80%"
  },
  blockText: {
    width: "100%"
  },
  block1: {
    display: "flex",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    flexDirection: "column",
    paddingTop: "10%",
    right: "0px"
  },
  block2: {
    width: "90%",
    display: "flex",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    flexDirection: "column",
    marginTop: "50px",
    right: "0px"
  },
  text: {
    display: "block",
    width: "90%",
    color: colors.black,
    fontSize: "72px",
    fontWeight: "bold",
    lineHeight: "60px",
    margin: "8px",
    zIndex: "99"
  },

  textMobile: {
    fontSize: "calc(20px + 4vw)",
    display: "block",
    width: "80%",
    color: colors.black,
    fontWeight: "bold",
    lineHeight: "calc(30px + 3vw)",
    margin: "8px",
    zIndex: "99"
  },
  selected: {
    backgroundColor: colors.black,
    color: colors.white,
    padding: "0 10px"
  }
};

const WhatIsSection = ({ classes, factor, offset, page, indexMotion }) => (
  <React.Fragment>
    <ParallaxLayer factor={factor} offset={offset} speed={0}>
      <div className={classes.componentDiv} />
    </ParallaxLayer>
    <ParallaxLayer factor={factor} offset={offset} speed={0.1}>
      <div className={classes.elementDiv}>
        <div className={classes.blockBackground} />
      </div>
    </ParallaxLayer>
    <ParallaxLayer factor={factor} offset={offset} speed={0.1}>
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <div className={classes.elementDiv}>
          <div className={`${classes.blockText} ${classes.block1}`}>
            <p className={classes.text}>{texts.S3_LINE1_TXT1}</p>
            <p className={classes.text}>{texts.S3_LINE2_TXT1}</p>
          </div>
        </div>
        <div className={page === 4 ? classes.elementDiv : classes.noDiv}>
          <div className={`${classes.blockText} ${classes.block2}`}>
            <p className={classes.text}>{texts.S3_LINE3_TXT1}</p>
            <p className={classes.text}>{texts.S3_LINE4_TXT1}</p>
            <p className={classes.text}>{texts.S3_LINE5_TXT1}</p>
          </div>
        </div>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <div className={classes.elementDiv}>
          <div className={`${classes.blockText} ${classes.block1}`}>
            <p className={classes.textMobile}>
              {`${texts.S3_LINE1_TXT1} ${texts.S3_LINE2_TXT1}`}
            </p>
            {/* <p className={classes.textMobile}>{texts.S3_LINE2_TXT1}</p> */}
          </div>
        </div>
        <div className={page === 4 ? classes.elementDiv : classes.noDiv}>
          <div className={`${classes.blockText} ${classes.block2}`}>
            <p className={classes.textMobile}>{texts.S3_LINE3_TXT1}</p>
            <p className={classes.textMobile}>{texts.S3_LINE4_TXT1}</p>
            <p className={classes.textMobile}>{texts.S3_LINE5_TXT1}</p>
          </div>
        </div>
      </Responsive>
    </ParallaxLayer>
    <ParallaxLayer factor={factor} offset={4} speed={0}>
      <div className={classes.componentDiv}>
        <div className={classes.ImageDiv}>
          {motions.map((e, i) => {
            return (
              <MotionComponent
                key={`t${i}`}
                index={i}
                isVisible={i === indexMotion}
              />
            );
          })}
        </div>
      </div>
    </ParallaxLayer>
  </React.Fragment>
);

WhatIsSection.propTypes = {
  classes: PropTypes.object,
  factor: PropTypes.number,
  indexMotion: PropTypes.number,
  offset: PropTypes.number,
  page: PropTypes.number
};

export default withStyles(styles)(WhatIsSection);
