import React from "react";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

import withStyles from "react-jss";
import colors from "../../constants/colors";
import "./modal.css";

const styles = {
  contentDiv: {
    width: "90%",
    color: colors.black,
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: "Graphik, sans-serif",
  },
  textDiv: {
    padding: "30px",
    width: "90%",

    color: colors.black,
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: "Graphik, sans-serif"
  }, 
  modalButton: {
    background:"transparent",
    border: "0px",
  }
};

const ModalItem = ({ classes, text, state, show, handleClose }) => (
  <div className={show ? "modal display-block" : "modal display-none"}>
    <section className="modal-main">
      <div className={classes.contentDiv}>
        <div className={classes.textDiv}>{text}</div>
      </div>
      <button type="button" className={classes.modalButton} onClick={handleClose}>
      <FontAwesomeIcon
            icon={faTimes}
            // className={classes.faTimes}
            style={{width:"24px", height:"24px"}}
        />
        
      </button>
    </section>
  </div>
);

ModalItem.propTypes = {
  classes: PropTypes.object,
  text: PropTypes.string,
  state: PropTypes.number,
  show: PropTypes.bool,
  handleClose: PropTypes.func
};

export default withStyles(styles)(ModalItem);
