import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Responsive } from "semantic-ui-react";

import NavigationItem from "./NavigationItem";
import NavigationItemMobile from "./NavigationItemMobile";

import menu from "../../constants/menu";
import colors from "../../constants/colors";

const styles = {
  componentDiv: {
    backgroundColor: colors.section1,
    overflow: "hidden",
    width: "100%",
    height: "100%",
    display: "block",
    position: "relative"
  },
  pagination: {
    margin: "0",
    position: "absolute",
    top: "46%",
    display: "block",
    width: "16px",
    height: "auto",
    right: "30px",
    "& div": {
      marginBottom: "16px",
      maxHeight: "16px !important"
    }
  },
  paginationMobile: {
    margin: "0",
    position: "absolute",
    top: "42%",
    display: "block",
    width: "5px",
    height: "auto",
    right: "12px",
    "& div": {
      marginBottom: "16px",
      maxHeight: "5px !important"
    }

  }
};

const RightNavigation = ({ classes, currentPage, onGoToPage }) => (
  <div className={classes.rightNavigation}>

    <Responsive minWidth={Responsive.onlyTablet.minWidth}>

    <ul className={classes.pagination}>
      {menu.map(e => {
        return (
          <NavigationItem
            key={e.i}
            i={e.i}
            offset={e.offset}
            text={e.text}
            currentPage={currentPage}
            onGoToPage={onGoToPage}
          />
        );
      })}
    </ul>

    </Responsive>

    <Responsive {...Responsive.onlyMobile}>
      <ul className={classes.paginationMobile}>
        {menu.map(e => {
          return (
            <NavigationItemMobile
              key={e.i}
              i={e.i}
              offset={e.offset}
              text={e.text}
              currentPage={currentPage}
              onGoToPage={onGoToPage}
              style= {classes.mobileNavItem}
            />
          );
        })}
      </ul>
    </Responsive>



  </div>
);

RightNavigation.propTypes = {
  classes: PropTypes.object,
  currentPage: PropTypes.number,
  onGoToPage: PropTypes.func
};

export default withStyles(styles)(RightNavigation);
