import React from "react";
import { useTrail, animated } from "react-spring";
import "./slider.css";

const items = [1, 2, 3, 4, 5];
const config = { mass: 2, tension: 2500, friction: 150 };

export default function BackgroundDiv(props) {
  const trail = useTrail(items.length, {
    config,
    opacity: props.state ? 1 : 1,
    x: props.state ? 0 : 90,
    height: props.state ? 0 : window.innerWidth/5,
    from: { opacity: 0, x: 0, height: window.innerHeight },
  });
  return (
    <div className="trails-main">
      <div >
        {trail.map(({ x, height, ...rest }, index) => (
          <animated.div
            key={items[index]}
            className="trails-text"
            style={{ ...rest, transform: x.interpolate(x => `rotate3d(1,0,0,${x}deg)`) }}
          >
            <animated.div>asdfasdf</animated.div>
          </animated.div>
        ))}
      </div>
    </div>
  );
}
