import React, { useState, useEffect } from "react";

import shuffle from "lodash/shuffle";
import { useTransition, animated as a, config } from 'react-spring'
import "./AnimatedGallery.css";
import mobgenners from "../../constants/mobgenners";
import logoImages from "../../constants/logoimages";

const IMG_NUM = 9;
const fixedShuffleIndex = (a, f) => {
  const list = a.reduce((acc, e, i) => {
    if (!f[i]) {
      acc.pos.push(i);
      acc.unfixed.push(e);
    };
    return acc;
  }, { pos: [], unfixed: [] });

  list.pos = shuffle(list.pos);

  return a.map((e, i) => {
    return f[i] ? e : list.unfixed[list.pos.indexOf(i)]
  })
}

export default function Gallery({minwidth}) {


  shuffle(mobgenners);
  const [items, set] = useState(mobgenners);
  const [imgNum, setImageNum] = useState(IMG_NUM);

  const [random01LogoPosition, setRandom] = useState(0);
  const [randomLogo, setLogoRandom] = useState(0);

  const [mobileRandom, setMobileRandom] = useState(0);
  


  useEffect(() => void setInterval(() => {
    let random = Math.floor(Math.random() * imgNum);
    random = random === 3 ? random+1 : random;
    random = random === 8 ? random-1 : random;
    setRandom(random);
    setLogoRandom(Math.floor(Math.random()*logoImages.length));
    setMobileRandom(Math.floor(Math.random()*2));

    // console.log(items);
    const f = [];

    // Inside displayed images
    const num = Math.floor(Math.random()*IMG_NUM);
    const num3 = Math.floor(Math.random()*IMG_NUM);

    // With another outside the array
    const num2 = IMG_NUM + Math.floor(Math.random()*(mobgenners.length - IMG_NUM));
    const num4 = IMG_NUM + Math.floor(Math.random()*(mobgenners.length - IMG_NUM));


    for(let i=0; i<items.length; i++) {
      if(i === num ||  i=== num2 || i === num3 || i=== num4) {
        f.push(false);
      } else {
        f.push(true);
      }
    }
    set(fixedShuffleIndex(items, f));
  }, 2000), []);

  function renderImageContent(src, index) {


    if(index === 3 || index === 8) {
      return (
        <div className="grid2x2">
          <div className="box box1"><img src={items[(index+3)%items.length]}  alt={`mobgenner${index+4}`} key={index+4} /></div>
          <div className="box box2"><img src={items[(index+4)%items.length]}  alt={`mobgenner${index+5}`} key={index+5} /></div>
          <div className="box box3"><img src={items[(index+5)%items.length]}  alt={`mobgenner${index+6}`} key={index+6} /></div>
          <div className="box box4"><img src={items[(index+6)%items.length]}  alt={`mobgenner${index+7}`} key={index+7} /></div>
        </div>
      );
    } else if (index < IMG_NUM) {

        if(index !== random01LogoPosition) {

          return (
            <div>
              <img src={src} alt={`mobgenner${index}`} key={index} />
            </div>
          );
        } else {

          return (
            <div>
              <img src={logoImages[randomLogo]} alt={`mobgenner${index}`} key={index} />
            </div>
          );
        }
    }
  };

  function renderMobileContent(src, index) {

    if(index === 0) {
      if(mobileRandom === 0) {
        return (
          <div>
            <img src={src} alt={`mobgenner${index}`} key={index} />
          </div>
        );
      } else {
          return (
            <div>
              <img src={logoImages[randomLogo]} alt={`mobgenner${index}`} key={index} />
            </div>
          );
      }
    } else if (index === 1) {
        return (
          <div className="grid2x2">
            <div className="box box1"><img src={items[(index+3)%items.length]}  alt={`mobgenner${index+4}`} key={index+4} /></div>
            <div className="box box2"><img src={items[(index+4)%items.length]}  alt={`mobgenner${index+5}`} key={index+5} /></div>
            <div className="box box3"><img src={items[(index+5)%items.length]}  alt={`mobgenner${index+6}`} key={index+6} /></div>
            <div className="box box4"><img src={items[(index+6)%items.length]}  alt={`mobgenner${index+7}`} key={index+7} /></div>
          </div>
      );
   } else {
     return;
   }
  }

  return (
    <div className="gallery-container">
      <div className="gallery-grid">
        {items.map(window.innerWidth >= minwidth ? renderImageContent : renderMobileContent)}
      </div>
    </div>
  );
}
